import { login } from '@/api/user'
import { getToken, setToken, delToken } from '@/utils/storage'

export default {
  namespaced: true,
  state () {
    return {
      token: getToken()
    }
  },
  mutations: {
    setUserToken (state, payload) {
      state.token = payload
      setToken(payload)
    },
    logout (state) {
      state.token = null
      delToken()
    }
  },
  actions: {
    async loginAction (context, payload) {
      const res = await login(payload)
      context.commit('setUserToken', res.data.token)
    }
  }
}
